<template>
  <div id="app">
    <div class="header">
    <span v-if="pd==1">新增合同<span style="color:red;">未审批</span></span>
    <span v-if="pd==2">新增合同<span style="color:red;">审批通过</span></span>
    <span v-if="pd==3">新增合同<span style="color:red;">审批未通过</span></span>
    <div class="header-right">
      <el-button size="small" type="warning" @click="Refresh">刷新</el-button>
    </div>
    </div>
    <!-- 下方列表显示 -->
    <el-row :gutter="10">
   <el-col :xs="6" :sm="6" :md="4" :lg="4" :xl="4">
      <div class="box">
          <ul>
            <li :class="pd==1?'list':''" @click="change(1)">未审批</li>
            <li :class="pd==2?'list':''" @click="change(2)">审批通过</li>
            <li :class="pd==3?'list':''" @click="change(3)">审批未通过</li>
          </ul>
      </div>
      </el-col>
       <el-col :xs="18" :sm="18" :md="20" :lg="20" :xl="20">
      <div class="box-cont">
          <notApproval v-if="pd==1" ref="notApproval"></notApproval>
          <yesSub v-if="pd==2" ref="yesSub"></yesSub>
          <noApproval v-if="pd==3" ref="noApproval"></noApproval>
      </div>
      </el-col>
      </el-row>
  </div>
</template>
<script>
import notApproval from './notApproval'//未审批
import yesSub from './yesSub'//审批通过
import noApproval from './noApproval'//审批未通过
export default {
  components:{
    notApproval,
    yesSub,
    noApproval
  },
  data(){
    return{
      pd:1
    }
  },
  methods:{
    change(number){//切换列表
      this.pd = number;
    },
    Refresh(){//刷新
      if(this.pd==1){
        this.$refs.notApproval.axios();
      }
      if(this.pd==2){
        this.$refs.yesSub.axios();
      }
      if(this.pd==3){
        this.$refs.noApproval.axios();
      }
    }
  }
}
</script>
<style scoped>
.header{
      display: flex;
      justify-content: space-between;
      padding: 10px 0;
    }
    .header span{
      vertical-align: middle;
      line-height: 32px;
    }
    .header .header-right{
      vertical-align: middle;
    }

    .box{
      text-align: center;
      border: 1px solid #eee;
    }
    .box ul .list{
      background: #3e3e3e !important;
      border-radius: 5px;
      color: white;
    }
    .box ul li{
      margin: 10px 10px;
      padding: 5px 0;
      cursor: pointer;
      transition: .5s all;
    }
    .box ul li:hover{
      background: #999;
      color: white;
      border-radius: 5px;
    }

    .box ul{
      list-style: none;
      margin: 0;
      padding: 0;
    }
</style>



