<template>
    <div id="app">
       <!-- 下方列表显示 -->
    <div class="table-bot">
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      >
      <el-table-column
        prop="bh"
        label="项目编号">
      </el-table-column>
      <el-table-column
        prop="sqdw"
        label="申请单位">
      </el-table-column>
      <el-table-column
        prop="name"
        label="项目名称">
      </el-table-column>
      <el-table-column
        prop="jg"
        label="预算价格">
      </el-table-column>
      <el-table-column
        prop="yj"
        label="立项依据">
      </el-table-column>
      <el-table-column
        prop="xq"
        label="详情">
        <template slot-scope="scope">
        <span class="det" @click="model(scope.row.id)">{{scope.row.xq}}<span></span></span>
      </template>
      </el-table-column>
    </el-table>
    <el-pagination
    background
    :page-size=pagesize
    :current-page=pagenum
    :hide-on-single-page="pds"
    @prev-click = 'upPage'
    @next-click = 'nextPage'
    @current-change	= 'currentPage'
    layout="prev, pager, next"
    :total=totalcount>
  </el-pagination>
    </div>
    <!-- 查看详情的模态框 -->
    <el-dialog
    :visible.sync="dialogVisible"
    width="1200px">
    <detailsContract :id="sysid"></detailsContract>
    <!-- 模态框底部 -->
    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
  </span>
</el-dialog>
    </div>
</template>
<script>
import detailsContract from './detailsContract'//上级项目详情
export default {
  components:{
    detailsContract
  },
    data() {
        return {
          tableData: [],
          pds:true,//分页判断1页的话不显示
          totalcount:null,//总页数
          pagenum:1,//第几页
          pagesize:10,//一页显示条数
          dialogVisible:false,//模态框开关
          sysid:'',//查看详情传的id
        }
      },
      methods:{
        axios(){//列表
          this.$axios.get(`/pack/getFinishApproveList?pageNum=${this.pagenum}&pageSize=${this.pagesize}`).then(res=>{
            console.log(res);
            if(res.data.code==200){
              var data = res.data.data;
              this.totalcount = Number(res.data.totalcount);
              this.tableData = [];
              for(let i = 0;i<data.length;i++){
                  this.tableData.push({
                    bh: data[i].projectNum,
                    dw:data[i].projectUnitId,
                    sfscgxm:data[i].isCaiGou,
                    name: data[i].projectName,
                    jg:data[i].projectBudgetPrice,
                    yj:data[i].projectBasis,
                    ldyi:data[i].leaderOpinion,
                    xmbyj:data[i].projectDepartmentOpinion,
                    jsdwyj:data[i].constructionOpinion,
                    nrzy:data[i].projectContent,
                    cbr:data[i].promoterId,
                    sqdw:data[i].projectForUnit,
                    date:data[i].startTime,
                    lxfj:data[i].projectAttachment,
                    id:data[i].sysId,
                    xq:'查看详情'
              })
              }
            }
            if(res.data.code==204){
              console.log('查询失败')
            }
          })
        },
        upPage(page){//上一页
        this.pagenum = page;
        this.axios();
      },
      nextPage(page){//下一页
        this.pagenum = page;
        this.axios();
      },
      currentPage(page){//点击数字翻页
        this.pagenum = page;
        this.axios();
      },
      model(id){//查看详情
        this.dialogVisible = true;
        this.sysid = id;
      },
      },
      mounted(){
        this.axios();
      }
}
</script>
<style scoped>
   .table-bot >>> .el-table .cell{
      text-align: center;
    }
    .table-bot >>> .el-table td{
      padding: 0;
    }
    .det{
      position: relative;
      display: inline-block;
      width: 100%;
      height: 100%;
      cursor: pointer;
      padding: 12px 0;
    }
    .det span{
      width: 0px;
      position: absolute;
      bottom: 10px;
      transform: translateX(-50%);
      left: 50%;
      transition: .5s all;
      display: inline-block;
      border-bottom: 1.5px solid red;
    }
    .det:hover{
      color: red;
    }
    .det:hover span{
      width: 60px;
    }
    /* 分页 */
  .table-bot >>> .el-pagination{
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }
</style>



