            console.log(res)
            this.contractArr = [];
          this.contractArr.push(res.data.data);
        })
        }
    },
    mounted(){
      this.details();
      this.tenderDetails();
      this.contractDetails();
    },
    watch:{
      id(){
        this.details();
        this.tenderDetails();
        this.contractDetails();
      }
    }
}
